@import "variables.scss";

.example {
  background-color: $background-block-example;
  padding: 25px 20px 40px;

  &__title {
    margin-bottom: 40px;
  }

  &__text-wrapper {
    font-family: $content-font;
    font-size: 14px;
    line-height: 18px;
    color: $color-text-primary;
    margin-bottom: 20px;
    text-align: justify;
  }

  &__list {
    display: flex;
    margin-bottom: 20px;
    justify-content: space-between;
    gap: 32px;
  }

  &__legend {
    font-family: $title-font;
    font-size: 14px;
    line-height: 20px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 20px;
  }

  &__progress {
    font-family: $title-font;
    font-size: 24px;
    line-height: 24px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 8px;
  }

  &__description {
    font-family: $content-font;
    font-size: 12px;
    line-height: 12px;
    color: $color-text-primary;
    text-align: center;
    width: 77px;
    margin-bottom: -11px;
    background-color: $background-block-example;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: $border-example-item;
    border-radius: 5px;
    min-width: 124px;
    width: 100%;
    padding-top: 14px;
  }
}
